import { MutableRefObject, useCallback, useRef } from "react";
import { useRecoilState } from "recoil";
import {
  ImageAverageColorAtom,
  ImgSrcAtom,
  TitleAtom,
} from "../../stores/titlemaker/titlemaker.store";
import { FastAverageColor } from "fast-average-color";

const useTitleMaker = () => {
  const [imgSrc, setImgSrc]: any = useRecoilState(ImgSrcAtom);
  const [title, setTitle] = useRecoilState(TitleAtom);
  const [averageColor, setAverageColor] = useRecoilState<string>(
    ImageAverageColorAtom
  );

  const imgRef: MutableRefObject<HTMLInputElement | null> = useRef(null);

  //파일 style 변경 위한
  const onClickImg = () => {
    if (imgRef.current) {
      imgRef.current.click();
    }
  };
  //파일 이미지 업로드
  const onUpload = (e: any) => {
    const file = e.target.files[0];
    const reader = new FileReader();
    reader.readAsDataURL(file);

    return new Promise<void>((resolve) => {
      reader.onload = () => {
        setImgSrc(reader.result || null);
        resolve();
      };
    });
  };

  const TitleChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      setTitle(e.target.value);
    },
    [setTitle]
  );

  //이미지 평균 컬러 뽑기
  const ImageColor = () => {
    const image = new Image();
    image.src = imgSrc;

    image.onload = () => {
      const fac = new FastAverageColor();
      const Color = fac.getColor(image).rgb;

      setAverageColor(Color);
    };
  };

  return { imgRef, onClickImg, onUpload, TitleChange, ImageColor };
};

export default useTitleMaker;
